<template>
  
  <div class="nodeboard">
    <div class="header-container">
        <div class="head">Interpretation</div>
        <div class="legend">
          <div>Ctrl+1</div>
          <div>Group</div>
        </div>
        <div class="legend">
          <div>Ctrl+2</div>
          <div>Text</div>
        </div>
        <div class="legend" style="margin-right: 10px;">
          <div>Ctrl+Wheel</div>
          <div>Zoom</div>
        </div>
        <div class="legend">
          <div>Alt+Click</div>
          <div>Pan</div>
        </div>
        <div class="legend">
          <div>Esc</div>
          <div>FullScreen</div>
        </div>
    </div>
    <hr style="border-top: 2px solid #333533;">
    <div id="container" @mousemove="getMousePosition"></div>
    
  </div>


</template>

<script>

//画布和自定义HTML节点
import { Graph, Shape } from '@antv/x6'
//自定义Vue节点
import { register } from '@antv/x6-vue-shape'
//对齐线
import { Snapline } from '@antv/x6-plugin-snapline'
//调整大小
import { Transform } from '@antv/x6-plugin-transform'
//框选
import { Selection } from '@antv/x6-plugin-selection'
//快捷键
import { Keyboard } from '@antv/x6-plugin-keyboard'
//历史操作
import { History } from '@antv/x6-plugin-history'
//复制粘贴
import { Clipboard } from '@antv/x6-plugin-clipboard'
//拖拽
import { Dnd } from '@antv/x6-plugin-dnd'

import TextNode from './nodes/text-node.vue'
import ParentNode from './nodes/parent-node.vue'

register({
  shape: 'text-node',
  width: 200,
  height: 100,
  component: TextNode,
})
register({
  shape: 'parent-node',
  width: 200,
  height: 100,
  component: ParentNode,
})

//设置连接桩的样式以及出现位置
const ports = {
  groups: {
    top: {
      position: 'top',
      attrs: {
        circle: {
          r: 6,
          magnet: true,
          stroke: '#5F95FF',
          strokeWidth: 1,
          fill: '#fff',
          style: {
            visibility: 'hidden',
          },
        },
      },
    },
    right: {
      position: 'right',
      attrs: {
        circle: {
          r: 6,
          magnet: true,
          stroke: '#5F95FF',
          strokeWidth: 1,
          fill: '#fff',
          style: {
            visibility: 'hidden',
          },
        },
      },
    },
    bottom: {
      position: 'bottom',
      attrs: {
        circle: {
          r: 6,
          magnet: true,
          stroke: '#5F95FF',
          strokeWidth: 1,
          fill: '#fff',
          style: {
            visibility: 'hidden',
          },
        },
      },
    },
    left: {
      position: 'left',
      attrs: {
        circle: {
          r: 6,
          magnet: true,
          stroke: '#5F95FF',
          strokeWidth: 1,
          fill: '#fff',
          style: {
            visibility: 'hidden',
          },
        },
      },
    },
  },
  items: [
    {
      group: 'top',
    },
    {
      group: 'right',
    },
    {
      group: 'bottom',
    },
    {
      group: 'left',
    },
  ],
}

export default {
  name: 'node-board',
  props: ['nodelist','taglist','isfull'],
  data() {
    return {
      mouseX: 0,
      mouseY: 0,
      // 防止长按快捷键连续添加node
      isNodeAdded: false,

      // graph_saved: null,
    }
  },
  watch:{
    isfull(newVal){
      const board = document.querySelector('.nodeboard');
      if(newVal === 1){
        board.style.width = '100%';
        this.graph.resize(board.scrollWidth);
      }
      else{
        board.style.width = '59%';
        this.graph.resize(window.innerWidth*0.59*0.99);
      }
    }
  },
  mounted() {
    //初始化画布
    const graph = new Graph({
      container: document.getElementById('container'),
      background: {
        color: '#fff',
      },
      //放大缩小
      mousewheel: {
        enabled: true,
        modifiers: 'ctrl'
      },
      //滑动画布
      panning: {
        enabled: true,
        modifiers: 'alt',
        eventTypes: ['leftMouseDown'],
      },

      //节点嵌套
      embedding: {
        enabled: true,
        findParent({ node }) {
          const bbox = node.getBBox();
          return this.getNodes().filter((node) => {
            const data = node.getData();
            if (data && data.parent) {
              const targetBBox = node.getBBox();
              return bbox.isIntersectWithRect(targetBBox);
            }
            return false;
          })
        }, 
      },

      // 交互连接
      connecting: {
        connector: { name: 'smooth' },
        snap: true, // 自动吸附
        allowBlank: false,
        allowLoop: false,
        allowNode: false,
        allowEdge: false,
        allowPort: true,
        allowMulti: true,
        createEdge() {
          return new Shape.Edge({
            attrs: {
              line: {
                stroke: "#8f8f8f",
                strokeWidth: 1,
              }
            },
            zIndex: 0,
            tools: ['edge-editor'],
          })
        },
      },

      //高亮
      highlighting: {
        embedding: {
          name: 'stroke',
          args: {
            padding: -1,
            attrs: {
              stroke: '#73d13d',
            },
          },
        },
      },

    })
    this.graph = graph
    
    //对齐线
    graph.use(
      new Snapline({
        enabled: true,
      }),
    )
    //调整大小
    graph.use(
      new Transform({
        resizing: {
          enabled: true,
          orthogonal: false,
          allowReverse: false,
          // preserveAspectRatio: true, //保持宽高比
        }
      })
    )

    //框选
    graph.use(
      new Selection({
        rubberband: true,
        rubberEdge: true,
        rubberNode: true,
        showNodeSelectionBox: true,
        pointerEvents: 'none',
        eventTypes: 'leftMouseDown'
        // modifiers: 'space',
      })
    )
    //快捷键
    graph.use(
      new Keyboard({
        enabled: true,
      }),
    )
    //历史操作
    graph.use(
      new History({
        enabled: true,
      }),
    )
    //复制粘贴
    graph.use(
      new Clipboard({
        enabled: true,
      }),
    )

    this.dnd = new Dnd({
      target: graph,
      scaled: false,
    })

    // graph.addNode({
    //   shape: 'parent-node',
    //   id: 123,
    //   x: 200,
    //   y: 200,
    //   ports: {
    //     ...ports
    //   },
    //   data: {
    //     parent: true,
    //   },
    //   zIndex: 0,
    // })

    // graph.addNode({
    //   shape: 'vadio',
    //   id: 1234,
    //   x: 400,
    //   y: 300,
    //   ports: {
    //     ...ports
    //   },
    //   data: {
    //     parent: false,
    //   },
    //   zIndex: 100,
    // })
    

/*------------------------------------事件监视--------------------------------------*/

    //控制连接桩显示/隐藏
    const showPorts = (ports, show) => {
      for (let i = 0, len = ports.length; i < len; i = i + 1) {
        ports[i].style.visibility = show ? 'visible' : 'hidden'
      }
    }
    //鼠标悬停显示连接桩
    graph.on('node:mouseenter', () => {
      const container = document.getElementById('container')
      const ports = container.querySelectorAll(
        '.x6-port-body',
      )
      showPorts(ports, true)
    })
    //鼠标离开隐藏连接桩
    graph.on('node:mouseleave', () => {
      const container = document.getElementById('container')
      const ports = container.querySelectorAll(
        '.x6-port-body',
      )
      showPorts(ports, false)
    })

    //高亮框选边
    graph.on('selection:changed', ({ added, removed }) => {
      added.forEach((value) => {
        if(value.shape=='edge'){
          value.attr('line/stroke', 'red');
        }
      })
      removed.forEach((value) => {
        if(value.shape=='edge'){
          value.attr('line/stroke', '#8f8f8f');
        }
      })
    })

    //选中元素置顶
    graph.on('node:selected', ({ node }) => {
      if(!node.data.parent){
        node.toFront();
      }
    })

    //node右键添加按钮
    graph.on('node:contextmenu', ({ node }) => {
      if(node.shape=='text-node') return;
      if(node.shape=='parent-node') return;
      if(node.shape=='video-node') return;
      if(node.shape.startsWith('api-node')) return;
      if(node.hasTool('button')){
        node.removeTools();
        return;
      }
      //搜索按钮
      if(node.data.class == 'npc'){
        node.addTools({
          name: 'button',
          args: {
            markup: [
              {
                tagName: 'rect',
                selector: 'button',
                attrs: {
                  width: 100,
                  height: 20,
                  rx: 4,
                  ry: 4,
                  fill: 'white',
                  stroke: '#9b9b9b',
                  'stroke-width': 2,
                  cursor: 'pointer',
                },
              },
              {
                tagName: 'text',
                selector: 'text',
                textContent: node.data.name,
                attrs: {
                  fill: '#9b9b9b',
                  'font-size': 10,
                  'text-anchor': 'middle',
                  'pointer-events': 'none',
                  x: 50,
                  y: 13,
                },
              },
            ],
            offset: { x: -110, y: 0 },
            //点击按钮事件
            onClick:() => {
              // node.removeTools();
              this.searchToolFunc(node, node.data.name)
            }
          }
        })
      }
      else{
        for(let i=0;i<node.data.keywords.length;i++){
          node.addTools({
            name: 'button',
            args: {
              markup: [
                {
                  tagName: 'rect',
                  selector: 'button',
                  attrs: {
                    width: 100,
                    height: 20,
                    rx: 4,
                    ry: 4,
                    fill: 'white',
                    stroke: '#9b9b9b',
                    'stroke-width': 2,
                    cursor: 'pointer',
                  },
                },
                {
                  tagName: 'text',
                  selector: 'text',
                  textContent: node.data.keywords[i],
                  attrs: {
                    fill: '#9b9b9b',
                    'font-size': 10,
                    'text-anchor': 'middle',
                    'pointer-events': 'none',
                    x: 50,
                    y: 13,
                  },
                },
              ],
              offset: { x: -110, y: 30*i },
              //点击按钮事件
              onClick:() => {
                // node.removeTools();
                this.searchToolFunc(node, node.data.keywords[i]);
              } 
            }
          })
        }
      }

      //添加tag按钮
      const nodeData = this.nodelist.find((x)=>x.id==node.data.idx);
      for(let i=0;i<nodeData.tags.length;i++){
        const tag = this.taglist.find((tag)=>tag.name==nodeData.tags[i]);
        let color='';
        if(tag.class=='npc') color='#5a9cf8';
        if(tag.class=='location') color='#7ec050';
        if(tag.class=='achievement') color='#dca550';
        if(tag.class=='other') color='#e47672';
        node.addTools({
          name: 'button',
          args: {
            markup: [
              {
                tagName: 'rect',
                selector: 'button',
                attrs: {
                  width: 100,
                  height: 20,
                  rx: 4,
                  ry: 4,
                  fill: color,
                  opacity: 0.1,
                  stroke: color,
                  'stroke-width': 2,
                  cursor: 'pointer',
                },
              },
              {
                tagName: 'text',
                selector: 'text',
                textContent: tag.name,
                attrs: {
                  fill: color,
                  'font-size': 10,
                  'text-anchor': 'middle',
                  'pointer-events': 'none',
                  x: 50,
                  y: 13,
                },
              },
            ],
            offset: { x: node.size().width+10, y: 30*i },
            //点击按钮事件
            onClick:() => {
              // node.removeTools();
              this.addTagFunc(tag.name);
            }            
          }
        })        
      }
    })

    // // 鼠标移开时删除按钮
    // graph.on('node:mouseleave', ({ node }) => {
    //   node.removeTools(); // 删除所有的工具
    // })


    //选中节点后不再显示浮动元素
    graph.on("node:click", () => {
      // graph.unselect(node);
      // node.removeTools();
      const container = document.getElementById('container');
      const ports = container.querySelectorAll(
        '.x6-port-body',
      );
      showPorts(ports, false);
    });

    graph.on("node:selected",({ node }) => {
      const nodeSelcted = graph.getSelectedCells();
      if(nodeSelcted.length == 1){
        graph.createTransformWidget(node);
      }
    })


    // 禁止图片节点resize
    // // resize 之前记录原始尺寸
    // graph.on('node:resize', ({ node }) => {
    //   node.prop('originSize', node.size());
    //   node.prop('originPosition', node.position());
    // })

    // // resize 之后还原
    // graph.on('node:resizing', ({ node }) => {
    //   if(node.shape!=='text-node' && node.shape!=='parent-node'){
    //     node.prop('size', node.prop('originSize'));
    //     node.prop('position', node.prop('originPosition'));
    //   }
    // })
    

    //边调整工具
    graph.on('edge:mouseenter', ({ cell }) => {
      cell.addTools({
        name: 'vertices',
        args: {
          attrs: { fill: '#8f8f8f' },
        },
      })
    })
    graph.on('edge:contextmenu', ({ cell })=>{
      if(cell.hasTool('vertices')){
        cell.removeTool('vertices');
      }
    })
    graph.on('edge:mouseleave', ({ cell })=>{
      if(cell.hasTool('vertices')){
        cell.removeTool('vertices');
      }
    })

    //双击节点将其所有tag添加到tagSelected(先清空)
    graph.on('node:dblclick', ({ node })=>{
      if(node.shape == 'parent-node') return;
      if(node.shape == 'text-node') return;
      if(node.shape.startsWith('api-node')) return;
      this.clearTagFunc();
      const nodeData = this.nodelist.find((x)=>x.id==node.data.idx);
      for(const tag of nodeData.tags){
        this.addTagFunc(tag);
      }
    })

    // group节点嵌入group节点时index变大 & 支持多选节点同时group
    graph.on('node:embedded', ({ node, currentParent})=>{
      if(node.shape === 'parent-node'){
        node.setZIndex(currentParent.zIndex + 1);
      }

      const nodes = graph.getSelectedCells().filter((node)=>node.isNode() && node.parent == undefined);
      for(const item of nodes){
        currentParent.addChild(item);
      }

    })



/*------------------------------------快捷键绑定--------------------------------------*/

    //删除框选内容
    graph.bindKey(['backspace', 'delete'], () => {
      const cells = graph.getSelectedCells();
      if (cells.length) {
        graph.removeCells(cells);
      }
    })

    //Ctrl+C复制
    graph.bindKey('ctrl+c', () => {
      const cells = graph.getSelectedCells();
      if(cells.length) {
        graph.copy(cells);
      }
      return false
    })

    //Ctrl+V粘贴
    graph.bindKey('ctrl+v', () => {
      if (!graph.isClipboardEmpty()) {
        const cells = graph.paste({ offset: 32 });
        graph.cleanSelection();
        graph.select(cells);
      }
      return false
    })

    //Ctrl+Z撤销操作
    graph.bindKey('ctrl+z', () => {
      graph.undo();
    })

    //Ctrl+1添加parent node
    graph.bindKey('ctrl+1', () => {
      if(!this.isNodeAdded){
          graph.addNode({
          shape: 'parent-node',
          x: this.mouseX-100,
          y: this.mouseY-50,
          ports: {
            ...ports
          },
          data: {
            parent: true,
          },
          zIndex: 0,
        })
      }
      //1000ms后才能再次添加
      this.isNodeAdded = true;
      setTimeout(() => { this.isNodeAdded = false; }, 1000);
    })

    //Ctrl+2添加text node
    graph.bindKey('ctrl+2', () => {
      if(!this.isNodeAdded){
          graph.addNode({
          shape: 'text-node',
          x: this.mouseX-100,
          y: this.mouseY-50,
          ports: {
            ...ports
          },
          data: {
            parent: false,
          },
          zIndex: 0,
        })
      }
      //1000ms后才能再次添加
      this.isNodeAdded = true;
      setTimeout(() => { this.isNodeAdded = false; }, 1000);
    })
    
    // 节点太多了 没有提前注册 难搞
    // graph.bindKey('ctrl+s', () => {
    //   localStorage.setItem('graph', JSON.stringify(graph.toJSON()));
    // })
    // this.graph_saved = JSON.parse(localStorage.getItem('graph'));
    // console.log(this.graph_saved);
    // if(this.graph_saved != null) graph.fromJSON(this.graph_saved);

    this.dnd = new Dnd({
      target: graph,
      scaled: false,
    })
  },

  created() {
    // 监听 'startDrag' 事件
    // 使用 .bind(this)确保startDrag方法在事件触发时,this指向正确的Vue实例。
    this.$bus.$on('startDrag', this.startDrag.bind(this)); 
  },

  beforeDestroy() {
    // 在组件销毁前移除监听，避免内存泄漏
    this.$bus.$off('startDrag', this.startDrag);
  },

  methods: {
    startDrag(e, node){
      const name = 'node-'+ node.id;
      let w = 160,h = 90;
      if(node.class == 'npc'){
        w = 100; h = 100;
      }
      Shape.HTML.register({
        shape: name,
        width: w,
        height: h,
        data: {
          idx: node.id,
          name: node.name,
          keywords: node.keywords,
          class: node.class,
        },
        html() {
          let str = 
          `<div class='img-node'>
            <img class='img' src="${node.src}">
            <div class='node-title'>${node.name}</div>
          </div>`
          return str;
        },
      })

      const mynode = this.graph.createNode({
        shape: name,
        ports: {
          ...ports
        },
        data: {
          parent: false,
          name: node.name,
          keyword: node.keywords,
        },
      });

      this.dnd.start(mynode, e);
    },

    //把节点的tag放进tagSelected
    addTagFunc(tagName){
      this.$bus.$emit('addTagFunc', tagName);
    },
    //清空tagSelected
    clearTagFunc() {
      this.$bus.$emit('clearTagFunc');
    },

    //搜索节点关键词
    async searchToolFunc(node, keyword){
      const name = 'api-node-' + keyword;
      const explanation = await this.getExplanation(keyword);
      Shape.HTML.register({
        shape: name,
        width: 300,
        height: 200,
        html() {
          let str =
          `<div class='api-node'>
            <div class='api-node-title'>${keyword}</div>
            <div class='api-node-content'>${explanation}</div>
          </div>`
          return str;
        },
      })

      const searchNode = 
      this.graph.addNode({
        shape: name,
        x: node.position().x,
        y: node.position().y+node.size().height+100,
        data: {
          parent: false,
        },
        zIndex: 100,
      })
      this.graph.addEdge({
        shape: 'edge',
        source: searchNode.id,
        target: node.id,
        attrs: {
          line: {
            strokeDasharray: 5,
          },
        },
      }) 
    },

    //调用API
    async getExplanation(keyword) {
      const response = await fetch("https://xiaoai.plus/v1/chat/completions", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "Authorization": `sk-mNp5RxndrCFyq8Ms9423Cd645b7948BbBf60689773013202`,
        },
        body: JSON.stringify({
          model: "gpt-3.5-turbo",
          messages: [{ role: "user", content: `Please explain the term: ${keyword}, limit it to 20 words` }],
          max_tokens: 100,
        }),
      });

      const data = await response.json();
      const explanation = data.choices[0].message.content;
      return explanation;
    },

    //获取鼠标相对于当前左上角的坐标
    getMousePosition(event){
      const location = this.graph.graphToLocal(event.offsetX, event.offsetY);
      this.mouseX = location.x;
      this.mouseY = location.y;
    },


  }

}

</script>

<style>

  #container {
    width: 100%;
    height: 100%;
    border-radius: 20px;
  }

  .nodeboard {
    display: flex;
    flex-direction: column;
    width: 59%;
    height: 100%;
    border: 2px solid #333533;
    border-radius: 20px;
  }

  .header-container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 15px;
  }

  .head {
    height: 60px;
    width: 100%;
    font-size: 30px;
    line-height: 60px;
    padding-left: 15px;
    color: #333533;
  }

  .legend{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 80%;
    width: 10vw;
    /* background-color: #111; */
    font:18px "Noto-Sans-Italic";
    align-items: center;
    color: #333533;
    margin-left: 10px;
  }

  .x6-widget-selection-box {
    border: none;
    box-shadow: none;
  }

  .img-node{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }

  .img{
    width: 95%;
    height: 95%;
    border-radius: 10px;
    /* margin-bottom: 10px; */
  }

  .node-title{
    font: 14px "Noto-Sans-Italic";
    margin-top: 5px;
    height: 20px;
    line-height: 20px;
    text-align: center;
  }

  .api-node{
    font: 16px "Noto-Sans-Italic";
    width: 100%;
    height: 100%;
    padding: 10px;
    border-radius: 10px;
    background-color: #b1b1b1;
    text-align: justify;
    /* border: 2px solid #1b1b1b; */
    resize: none;
    overflow: auto;
    outline: none;
    position: relative;
  }

  .api-node-title{
    font: 22px "Noto-Sans-Bold-Italic";
    height: 20px;
    line-height: 20px;
    margin-bottom: 5px;
  }
  
  .video-node{
    width: 100%;
    height: 100%;
    /* border: 2px solid #333533; */
    border-radius: 6px;
    /* pointer-events: none; */
  }

</style>