<template>
  <div class="wrapper">

    <div class="left">
      <div class="search">
        <div class="search-bar">
          <i class="el-icon-search"></i>
          <input type="text" placeholder="Search Tag" v-model="searchTerm" @input="onInput">
          <i class="el-icon-delete" @click="tagClear"></i>
        </div>
        <div v-if="this.tagSuggested.length && this.searchTerm" class="suggestion-taglist">
          <el-tag v-for="tag in tagSuggested" :key="tag.id"
              @click="tagAdd(tag.name)"
              effect="plain"
              type="info"
              style="font-size: 13px;"
              class="suggestion-item">
              {{ tag.name }}
          </el-tag>
        </div>
      </div>
      <div class="tag-box hide-scrollbar">
        <VueDraggable class="tag-box-in" v-model="tagSelected" @update:modelValue="listRefresh">
          <el-tag v-for="tag in tagSelected" :key="tag.id" :type="getTagColor(tag.name)"
                  closable
                  @close="tagDelete(tag.name)"
                  style="margin-top: 6px;
                        margin-left: 10px;
                        font-size: 15px;"> {{ tag.name }}
          </el-tag>
        </VueDraggable>
      </div>
      <div class="title">Classification</div>
      <div class="button-group">
        <div class="button-groupin">
          <div class="row">
            <button class="mybutton" 
                    @click="changeTag('npc')"
                    style="width: 50%; 
                    border-right: 3px solid #b1b1b1;">Character</button>
            <button class="mybutton" 
                    @click="changeTag('location')"
                    style="width: 50%;">Location</button>
          </div>
          <div class="row">
          <button class="mybutton" 
                  @click="changeTag('achievement')"
                  style="width: 100%;
                  border-top: 3px solid #b1b1b1;">Achievement</button>
          </div>
        </div>
      </div>

      <div class="tag-list hide-scrollbar">
        <div class="tag-listin">
          <div class="tag-item"  
                  v-for="tag in taglistNow" :key="tag.id"
                  @click="tagAdd(tag.name)">
            <img class="tag-item-img" :src=tag.src>
            <div class="tag-item-name">{{ tag.name }}</div>
            <div class="tag-item-desc" v-if="tag.class=='achievement'">{{ tag.description }}</div>
          </div>
        </div>
      </div>
    </div>
    <div class="right">
      <div class="title" style="border-radius: 0px 20px 0px 0px;">Recommended Elements</div>
      <div class="button-group">
        <div class="button-groupin">
          <div class="row">
            <button class="mybutton" 
                    @click="changeClass('cut-scene')"
                    style="width: 33%;">Cut-scene</button>
            <button class="mybutton" 
                    @click="changeClass('npc')"
                    style="width: 33%;
                    border-left: 3px solid #b1b1b1;
                    border-right: 3px solid #b1b1b1;
                    font-size:20px;">Related Character</button>
            <button class="mybutton" 
                    @click="changeClass('environment')"
                    style="width: 34%;">Environment</button>
          </div>
          <div class="row">
            <button class="mybutton" 
                    @click="changeClass('artifact')"
                    style="width: 33%;
                    border-top: 3px solid #b1b1b1;">Artifact</button>
            <button class="mybutton" 
                    @click="changeClass('text')"
                    style="width: 33%;
                    border-left: 3px solid #b1b1b1;
                    border-right: 3px solid #b1b1b1;
                    border-top: 3px solid #b1b1b1;">Text</button>
            <button class="mybutton" 
                    @click="changeClass('dialogue')"
                    style="width: 34%;
                    border-top: 3px solid #b1b1b1;">Dialogue</button>
          </div>
        </div>
      
      </div>
      <div class="sortby">
        Sort by: 
        <span :class="{'selected-sort': sortByTime == true}"
              @click="sortModeChange('time')"
              style="cursor: pointer;">Time</span> 
        / 
        <span :class="{'selected-sort': sortByRelevance == true}"
              @click="sortModeChange('relevance')"
              style="cursor: pointer;">Relevance</span>
      </div>

      <div class="node-list hide-scrollbar">
        <ul class="node-listin">
          <li class="node-item" 
              v-for="node in nodelistNow" :key="node.id">
            <div class="node-item-content" @mousedown="StartDrag($event, node)">
              <img class="node-item-img" :src="node.src" style="height: 150px;border-radius: 20px;">
              <div class="name">{{ node.name }}</div>
              <div class="keywords">
                <div v-for="(word, idx) in node.keywords" :key="idx" style="margin-right: 8px;">{{ word }}</div>
              </div>
            </div>
            <div class="node-item-tags hide-scrollbar">
              <el-tag v-for="(tag, idx) in node.tags" :key="idx" :type="getTagColor(tag)"
                      @click="tagAdd(tag)">
                {{ tag }}
              </el-tag>
              <input
                class="input-new-tag"
                v-if="node.inputVisible"
                v-model="node.inputValue"
                @keyup.enter="handleInputConfirm(node)"
                @blur="handleInputConfirm(node)">
              <button v-else class="button-new-tag" @click="showInput(node)">+</button>
            </div>
          </li> 
        </ul>
      </div>
    </div>

  </div>
</template>

<script>
  import { VueDraggable } from 'vue-draggable-plus';

  export default {
    name: 'NodeList',
    props: ['nodelist','taglist'],
    components: {
      VueDraggable,
    },
    data() {
      return {

        //tag search的input和搜索的taglist
        searchTerm: '',
        tagSuggested: [],

        sortByTime: false,
        sortByRelevance: true,
        classSelected: '',
        tagSelected: [],


        nodelistNow: [],
        taglistNow: [],

      }
    },

    computed: {
      // fittingHeightTag() {
      //   const length = this.taglistNow.length;
      //   return {
      //     height: (500/length)+'px',
      //     lineHeight: (500/length)+'px',
      //   };
      // },

    },

    methods: {

      StartDrag(event, node) {
        this.$bus.$emit('startDrag', event, node); // 通过 $bus 触发 'startDrag' 事件
      },

      onInput(){
        //更新搜索tag列表
        this.tagSuggested = this.tagSuggest();
      },

      //删选前缀匹配的tag
      tagSuggest(){
        if(this.searchTerm=='') return [];
        return this.taglist.filter((tag) =>
          tag.name.toLowerCase().startsWith(this.searchTerm.toLowerCase()));
      },

      //修改选中的tag类别
      changeTag(tagClass){
        this.taglistNow = this.taglist.filter((tag)=>tag.class==tagClass);
        //滚动条归位
        this.$nextTick(() => {
          const nodeListElement = this.$el.querySelector('.tag-list');
          if(nodeListElement){
            nodeListElement.scrollTop = 0;
          }
        });
      },

      //添加标签
      tagAdd(tagName){
        //先清空serach的输入
        this.searchTerm = '';
        // tag不能重复添加
        let tag = this.tagSelected.find((tag)=>tag.name==tagName);
        if(tag == undefined){
          tag = this.taglist.find((tag)=>tag.name==tagName);
          this.tagSelected.push(tag);
          this.listRefresh();
        }
      },

      //清空标签
      tagClear(){
        this.tagSelected = [];
        this.listRefresh();
      },

      //删除标签
      tagDelete(tagName){
        this.tagSelected = this.tagSelected.filter((tag)=>tag.name!==tagName);
        this.listRefresh();
      },

      //更新选中的class
      changeClass(className){
        this.classSelected = className;
        this.listRefresh();
      },

      //切换排序方式
      sortModeChange(type) {
        if(type == 'time'){
          this.sortByTime = 1 - this.sortByTime;
        }
        if(type == 'relevance'){
          this.sortByRelevance = 1 - this.sortByRelevance;
        }
        this.listRefresh();
      },

      //据当前tag和class的选择刷新界面
      listRefresh(){
        this.nodelistNow = this.nodelist.filter((node)=>(node.class==this.classSelected));
        this.sortBy();
        //滚动条归位
        this.$nextTick(() => {
            const nodeListElement = this.$el.querySelector('.node-list');
            if (nodeListElement) {
                nodeListElement.scrollTop = 0;
            }
            const tagBoxElement = this.$el.querySelector('.tag-box');
            if (tagBoxElement) {
              tagBoxElement.scrollTop = tagBoxElement.scrollHeight;
            }
        });
      },

      //根据选定的排序规则排序
      sortBy() {
        // console.log(this.tagSelected);
        if(this.sortByTime && !this.sortByRelevance){
          if(this.classSelected != 'npc'){
            this.nodelistNow.sort((a, b) => this.getTimestamp(a.src) - this.getTimestamp(b.src));
          }
        } 
        else if(!this.sortByTime && this.sortByRelevance){
          this.nodelistNow.sort((a, b) => {
            const aMatchCount = this.getMatchCount(a);
            const bMatchCount = this.getMatchCount(b);
            if(aMatchCount !== bMatchCount){
              return bMatchCount - aMatchCount;
            } 
            else{
              return this.compareByTagPriority(a.tags, b.tags);
            }
          });
        }
        else if(this.sortByTime && this.sortByRelevance){
          this.nodelistNow.sort((a, b) => {
            const aMatchCount = this.getMatchCount(a);
            const bMatchCount = this.getMatchCount(b);
            if(aMatchCount !== bMatchCount){
              return bMatchCount - aMatchCount;
            } 
            else{
              const byRelevance = this.compareByTagPriority(a.tags, b.tags);
              if(byRelevance != 0){
                return byRelevance;
              }
              else return this.getTimestamp(a.src) - this.getTimestamp(b.src);
            }
          });
        }
      },


      // 提取timestamp
      getTimestamp(src) {
          const match = src.match(/Screenshot_(\d{8})_(\d{6})/);
          // console.log(match[1]+match[2])
          if (match) return match[1]+match[2];
          else{
            console.log("TimeStamp Error!");
            console.log(src);
            return null;
          }
          
      },

      // 返回当前node与tagSelected中的tag匹配数
      getMatchCount(node){
        let cnt = 0;
        for(const tag of this.tagSelected){
          for(const tagName of node.tags){
            if(tag.name == tagName){
              cnt=cnt+1;
              break;
            }
          }
        }
        return cnt;
      },

      compareByTagPriority(tagsA, tagsB){
        for(const selectedTag of this.tagSelected){
          const isATagIn = tagsA.includes(selectedTag.name);
          const isBTagIn = tagsB.includes(selectedTag.name);
          if(isATagIn && !isBTagIn){
            return -1;
          } 
          else if(!isATagIn && isBTagIn){
            return 1;
          }
        }
        return 0;
      },

      // 给node添加tag
      showInput(node) {
        node.inputVisible = true;
        this.$nextTick(() => {
          const inputElement = document.querySelector('.input-new-tag');
          if (inputElement) {
            inputElement.focus();
          }
        })
      },
      handleInputConfirm(node) {
        let inputValue = node.inputValue;
        if (inputValue) {
          // 用filter完的引用直接改父组件的变量... 不规范 但是方便...
          // node.tag.push(inputValue);
          this.$emit('addTag2Node', node.id, inputValue);
        }
        node.inputVisible = false;
        node.inputValue = '';
      },

      getTagColor(tagName){
        const tag = this.taglist.find((tag)=>tag.name==tagName);
        // console.log(tagName);
        if(tag.class=='npc') return 'none';
        if(tag.class=='location') return 'success';
        if(tag.class=='achievement') return 'warning';
        if(tag.class=='other') return 'danger';
      }

    },

    created() {
      this.$bus.$on('addTagFunc', this.tagAdd);
      this.$bus.$on('clearTagFunc', this.tagClear);
    },
    beforeDestroy() {
      this.$bus.$off('addTagFunc', this.tagAdd);
      this.$bus.$off('clearTagFunc', this.tagClear);
    },

    watch: {
      nodelistNow: {
        handler() {
          this.$nextTick(() => {
            const listItems = document.querySelectorAll('.node-item');
            listItems.forEach((item) => {
              const imgHeight = item.querySelector('.node-item-img').scrollHeight;
              const nameHeight = item.querySelector('.name').scrollHeight;
              const keywordHeight = item.querySelector('.keywords').scrollHeight;
              const minHeight = imgHeight + nameHeight + keywordHeight;
              // console.log(minHeight);
              item.style.minHeight = `${minHeight + 50}px`;
              item.style.maxHeight = `${minHeight + 50}px`;
            });
          })
        },
        deep: true,
      },
    },

    mounted() {

    },
  }
</script>

<style>

.wrapper {
    display: flex;
    width: 40%;
    height: 100%;
    border: 2px solid #333533;
    border-radius: 20px;
  }

.left{
  display: flex;
  flex-direction: column;
  width:40%;
  border-right: 2px solid #333533;

}

.search{
  position:relative;
  height: 60px;
}

.search-bar{
  display: flex;
  align-items: center;
  height: 100%;
  width: 100%;
}

.search-bar .el-icon-search{
  font-size: 35px;
  margin-left: 15px;
}

.search-bar input{
  line-height: 60px;
  width: 60%;
  font: 28px "Noto-Sans-Bold-Italic";
  padding-left: 15px;
}

.search-bar .el-icon-delete{
  font-size: 25px;
  text-align: right;
  width: 20%;
  padding-right: 10px;
}

.suggestion-taglist{
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  top: 63px;
  max-height: 100px;
  width: 100%;
  z-index: 1000;
  background-color: #fff;
}

.suggestion-item{
  margin-top: 10px;
  margin-left: 10px;
  cursor: pointer;
}

.suggestion-item :hover{
  color: #f0f0f0;
  background-color: #1b1b1b;
}

.tag-box{
  height: 160px;
  border-top: 2px solid #333533;
  border-bottom: 2px solid #333533;
  overflow: auto;
}

.tag-box-in{
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  height: 155px;
}

.title{
  font: 30px "Noto-Sans-Bold-Italic";
  text-align: center;
  height: 60px;
  line-height: 60px;
  background-color: #ffdd66;
}

.button-group{
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100px;
  border-top: 2px solid #333533;
  border-bottom: 2px solid #333533;
}

.button-groupin{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 90%;
  width: 95%;
}

.row{
  display: flex;
  height: 50%;
}

.mybutton{
  padding: 0;
  font: 22px "Noto-Sans-Bold-Italic";
  line-height: 22px;
  text-align: center;
  color: #333533;
}

.tag-list{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  flex: 1;
  width: 100%;
  overflow: auto;
}

.tag-listin{
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  width: 90%;
  padding-bottom: 10px;
}

.tag-item{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  min-height: 170px;
  width: 100%;
  margin-top: 0px;
  border-bottom: 2px solid #b1b1b1;
  overflow: auto;
}

.tag-item-img{
  height: 100px;
  margin-top: 5px;
  width: auto;
  border-radius: 10px;
}

.tag-item-name{
  font-size: 20px;
  text-align: center;
}

.tag-item-desc{
  font: 12px 'Noto-Sans-Italic';
  margin-bottom: 5px;
  text-align: center;
}

.right{
  width: 60%;
  display: flex;
  flex-direction: column;
}

.sortby{
  width: 100%;
  height: 40px;
  font-size: 22px;
  color: #b1b1b1;
  line-height: 40px;
  text-align: right;
  padding-right: 15px;
}

.selected-sort {
  color: #333533;
  font-weight: bold;
}

.node-list{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  flex: 1;
  overflow: auto;
}

.node-listin{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 95%;
  height: 100%;
}

.node-item{
  display: flex;
  justify-content: space-around;
  width: 95%;
  padding-top: 20px;
  border-bottom: 2px solid #b1b1b1;
  height: 600px;
}

.node-item-content{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 60%;
  margin-bottom: 10px;
}

.name{
  font: 15px "Noto-Sans-Italic";
}

.keywords{
  display: flex;
  font: 14px "Noto-Sans-Italic";
}

.node-item-tags{
  display: flex;
  flex-direction: column;
  align-items: end;
  overflow: auto;
  margin-bottom: 10px;
  width: 40%;
}

.node-item-tags .el-tag{
  font-size:14px; 
  width: 140px; 
  height: 40px;
  line-height: 40px;
  text-align: center;
  padding: 0;
  margin-bottom: 10px;
}

.button-new-tag{
  width: 140px;
  font: 30px "Noto-Sans-Bold-Italic";
  color: #b1b1b1;
  height: 40px;
  line-height: 40px;
  border:1px solid #b1b1b1;
  border-radius: 5px;
  text-align: center;
  margin-bottom: 10px;
}

.input-new-tag{
  width: 140px;
  font: 24px "Noto-Sans-Bold-Italic";
  height: 40px;
  line-height: 40px;
  text-align: center;
  border:1px solid #b1b1b1;
  border-radius: 5px;
}
</style>