<template>
  <div id="app">
    <div class="banner">
      <div>ClueCart with: </div>
      <el-select v-model="gameSelected" 
                 placeholder="Select Game">
        <el-option v-for="item in options" :key="item.id" :value="item.name">
        </el-option>
      </el-select>
    </div>
    <div class="cluecart">
      <nodelist :nodelist="nodelist" :taglist="taglist" @addTag2Node="addTag2Node" v-show="isfull===0"></nodelist>
      <nodeboard :nodelist="nodelist" :taglist="taglist" :isfull="isfull"></nodeboard>
    </div>

  </div>
</template>

<script>
  import nodelist from './components/node-list.vue';
  import nodeboard from './components/node-board.vue';
  // import data from '../public/data.json';
  import knight_data from '../public/knight.json';
  import monkey_data from '../public/monkey.json';

  export default {
    name: 'App',
    components: {
      nodelist,
      nodeboard,
    },
    data() {
      return {
        isfull: 0,
        gameSelected: '',
        options:[
          {id: 1, name: 'Hollow Knight'},
          {id: 2, name: 'Do Not Feed The Monkeys'},
        ],

        // taglist: data.taglist,
        // nodelist: data.nodelist,

        taglist: [],
        nodelist: [],
      };
    },
    // watch: {
    //     gameSelected(newVal) {
    //       localStorage.setItem('game', newVal);
    //       location.reload();
    //     }
    // },
    methods: {
      // 给节点加新标签，顺便更新taglist
      addTag2Node(nodeId, tagName){
        for(const node of this.nodelist){
          if(node.id === nodeId){
              if(!node.tags.includes(tagName)){
                  node.tags.push(tagName);
              }
            break;
          }
        }
        if(this.taglist.find((tag)=>tag.name==tagName) == undefined){
          this.taglist.push({id: this.taglist.length, name:tagName, class:'other'});
        }
      },

      disableMouseEvents() {
        document.addEventListener('mousedown', this.preventMouseEvent, true);
        document.addEventListener('contextmenu', this.preventMouseEvent, true);
        document.addEventListener('click', this.preventMouseEvent, true);
        document.addEventListener('dblclick', this.preventMouseEvent, true);
        document.addEventListener('wheel', this.preventMouseEvent, { passive: false });
        document.addEventListener('mousemove', this.preventMouseEvent, true);
        document.addEventListener('dragstart', this.preventMouseEvent, true);
        document.addEventListener('contextmenu', this.preventRightClick, true);
        document.addEventListener('keydown', function (event) {
          if((event.ctrlKey || event.metaKey) && (event.key === '1' || event.key === '2' || event.key === 's')){
                event.preventDefault();
          }
        }, true);
      },

    },

    mounted(){
      this.disableMouseEvents();

      document.addEventListener('keydown', (event) => {
        if(event.key === 'Escape' || event.key === 'Esc'){
            this.isfull = 1 - this.isfull;
        }
      }, true);

      this.gameSelected = localStorage.getItem('game');
      if(this.gameSelected === 'Hollow Knight'){
        this.taglist = knight_data.taglist;
        this.nodelist = knight_data.nodelist;
      } 
      else if(this.gameSelected === 'Do Not Feed The Monkeys'){
        this.taglist = monkey_data.taglist;
        this.nodelist = monkey_data.nodelist;
      } 
      else{
        this.gameSelected = '';
        this.taglist = [];
        this.nodelist = [];
      }

      this.$watch('gameSelected', (newVal) => {
        localStorage.setItem('game', newVal);
        location.reload();
      });
    }
  
  };

</script>
<style>
  #app {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    height: 100%;
  }

  .banner{
    display: flex;
    justify-content:flex-start;
    align-items: center; /* 垂直居中 */
    height: 2vh;
    font-size: 20px;
    padding-left: 10px;
    background-color: #ffffff;
  }

  .el-select input {
    font: 18px 'Noto-Sans-Italic';
    height: 2vh;
    width: 250px;
    padding-left: 10px;
    border: none;
  }

  .el-select .el-input__suffix{
    display: none;
  }

  .el-select-dropdown__item{
    font: 14px 'Noto-Sans-Bold-Italic';
    height: 30px;
    line-height: 30px;
    width: 250px;
  }
  .cluecart{
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 98vh;
  }

  @font-face {
    font-family: 'Noto-Sans-Bold-Italic';
    src: url('../src/assets/font/Noto-Sans-Bold-Italic.ttf'),
  }
  @font-face {
    font-family: 'Noto-Sans-Italic';
    src: url('../src/assets/font/Noto-Sans-Italic.ttf'),
  }
</style>